/**
 * @hyky/pond
 * ----------------------------------------------------------------------
 * Opinionated CSS reset, mostly inspired by Bootstrap's Reboot, but also 
 * takes some from `minreset`, `fix.css` and Eric Meyer's `reset.css`.
 * 
 * @author    Fabio Goto <fabio@hyky.games>
 * @author    Robert RÃ¼diger <robert@hyky.games>
 * @since     0.0.1
 */
html {
  box-sizing: border-box;

  // Prevents "fixed" elements from overlaying browser sidebars
  overflow: auto;

  // Prevents automatically zooming of fonts in mobile browsers
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: inherit;

  // Consistennt, browser specific, font-smoothing
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}

* {
  &,
  &:before,
  &:after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: 400;
}

b,
strong {
  font-weight: 700;
}

i,
em,
cite {
  font-style: oblique;
}

small {
  font-size: 80%;
}

pre,
code,
kbd,
samp,
var {
  font-size: 1rem;
}

pre {
  word-wrap: break-word;
  white-space: pre-wrap;

  code {
    color: inherit;
    background: transparent;
    border: 0;
    margin: 0;
    padding: 0;
  }
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

ul,
ol {
  list-style: none;
}

dt {
  font-weight: 700;
}

dd {
  margin: 0 0 .5rem;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

figure {
  margin: 0 0 1rem;

  &:last-child {
    margin-bottom: 0;
  }
}

img,
video {
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th,
td {
  padding: 0;
}

input,
select,
button,
textarea {
  margin: 0;

  &:focus {
    outline: none;
  }
}

select {
  &[multiple] {
    min-height: 6rem;
  }
}

textarea {
  resize: vertical;
}

[hidden] {
  display: none !important;
}

// Firefox
// ----------------------------------------------------------------------

::-moz-focus-inner {
  border: 0;
  padding: 0;
}
