@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;600;700&display=swap);
html{box-sizing:border-box;overflow:auto;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%}body{font-family:inherit;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased}article,aside,dialog,figcaption,figure,footer,header,hgroup,main,nav,section{display:block}*,*:before,*:after{box-sizing:inherit;margin:0;padding:0}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:400}b,strong{font-weight:700}i,em,cite{font-style:oblique}small{font-size:80%}pre,code,kbd,samp,var{font-size:1rem}pre{word-wrap:break-word;white-space:pre-wrap}pre code{color:inherit;background:transparent;border:0;margin:0;padding:0}sub,sup{font-size:75%;line-height:0;vertical-align:baseline;position:relative}sub{bottom:-.25em}sup{top:-.5em}ul,ol{list-style:none}dt{font-weight:700}dd{margin:0 0 .5rem}hr{box-sizing:content-box;height:0;overflow:visible}figure{margin:0 0 1rem}figure:last-child{margin-bottom:0}img,video{max-width:100%;height:auto}iframe{border:0}table{border-collapse:collapse;border-spacing:0}th,td{padding:0}input,select,button,textarea{margin:0}input:focus,select:focus,button:focus,textarea:focus{outline:none}select[multiple]{min-height:6rem}textarea{resize:vertical}[hidden]{display:none !important}::-moz-focus-inner{border:0;padding:0}html,body{color:#77a09b;background:#eae1bd;font-family:"Inter", sans-serif}body{text-align:center}img{display:block;margin:auto;margin-bottom:1rem}h1{color:#537773;font-size:4rem;font-weight:400;margin-bottom:1rem}h4{font-size:1.5rem;text-transform:uppercase}hr{border:0;background:#95b5b1;width:100%;height:1px;margin:1rem 0}p{margin-bottom:1rem}.lead{font-size:1.25rem;font-weight:300}.ftc-main{padding:5rem}

